import React, {Component} from 'react';

class Sample  extends Component {
    state = {
        a: 'Hello'
    };

    handleButtonClick = () => {
        console.log("Button pressed");
        this.setState({
            a: "Button pressed"
        });
    };

    render() {
        return (
            <div>
                <h1>{this.state.a}</h1>
                <button type="button" onClick={this.handleButtonClick}>Click Me ;)</button>
            </div>
        );
    }
}

export default Sample;